


import { NavBar, Carousel } from 'antd-mobile';
export default class Head extends Component {
    constructor() {
        super();
        this.state = {
            banner: [
                require('../../../assets/images/bg1.jpg'),
                require('../../../assets/images/bg2.jpg'),
                require('../../../assets/images/bg3.jpg'),
                require('../../../assets/images/bg4.jpg'),
            ],
            msgArr: [
                '认证龙头企业',
                '14年品牌',
                '3小时',
                '最近332046条好评'
            ]
        }
    }
    render() {
        return (
            <div >
                <NavBar
                    style={{ background: '#435448' }}
                    mode="light"
                    icon={<i key={1} className={"iconfont icon-icon04"} style={{ color: 'white', fontSize: 20 }}></i>}
                    onLeftClick={() => console.log('onLeftClick')}
                    rightContent={[
                        <i key={2} className={"iconfont icon-kefu"} style={{ color: 'white', fontSize: 20 }}></i>
                    ]}
                >
                    <span style={{ color: '#fd734d', fontSize: 20, fontWeight: 600 }}>
                        HUA.COM花礼网
                    </span>
                </NavBar>
                <Carousel
                    autoplay={true}
                    infinite
                    autoplayInterval={1500}
                    beforeChange={(from, to) => console.log(`slide from ${from} to ${to}`)}
                    afterChangetruncateSync
                >
                    {this.state.banner.map((val, i) => {
                        return (
                            <a
                                key={i}
                                href="#/index/home"
                                style={{ display: 'inline-block', width: '100%', height: 200 }}
                            >
                                <img
                                    src={val}
                                    style={{ width: '100%', verticalAlign: 'top', height: 200 }}
                                    onLoad={() => {
                                        window.dispatchEvent(new Event('resize'));
                                    }}
                                />
                            </a>
                        )
                    })}
                </Carousel>
                <div style={{ display: 'flex', justifyContent: 'space-around' }}>
                    {
                        this.state.msgArr.map((item, i) => {
                            return (
                                <div key={i}>
                                    <i className='iconfont icon-duigou' ></i>
                                    {item}
                                </div>
                            )
                        })
                    }
                </div>
            </div>
        )
    }
}